.contact {
  min-height: 458px;
  position: relative;
  &::before {
    content: "";
    position: absolute;
    inset: 0 0 0 0;
    background-image: linear-gradient(96.79deg, rgba(98, 73, 28, 0.4) 44.68%, rgba(49, 32, 2, 0) 85.21%),
      url("../../../public/img/contact/background.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    z-index: -1;
  }
  &__container {
    padding-top: 100px !important;
    padding-bottom: 100px !important;
  }
  &__content {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 30px;
    width: 100%;
  }
}

.content {
  max-width: 878px;
  &__title {
    font-size: 36px;
    line-height: 43.2px;
    color: var(--white);
  }
  &__description {
    font-size: 18px;
    line-height: 21.6px;
    color: var(--white);
    max-width: 715px;
    margin-bottom: 14px;
  }
  &__form {
    display: flex;
    gap: 27px;
  }
}

.form {
  &__input {
    width: 100%;
    padding: 25px 40px;
    line-height: 21.6px;
    color: var(--dark-gray);
    font-size: 18px;
    border-radius: 10px;
    min-width: 280px;
    letter-spacing: 2px;
    transition: outline var(--speed_02), box-shadow var(--speed_02);
    &::placeholder {
      color: var(--dark-gray);
    }
    &.error {
      outline: 3px solid var(--light-red);
      box-shadow: 0px 0px 15px var(--light-red);
    }
  }
  &__submit {
    font-size: 18px;
    line-height: 21.6px;
    padding: 25px 38px;
    border-radius: 10px;
    color: var(--white);
    background-color: var(--dark-yellow);
    min-width: 300px;
    transition: box-shadow var(--speed_04);
    cursor: pointer;
    &:hover {
      box-shadow: 0px 0px 60px rgba(255, 194, 86, 0.5);
    }
  }
}

@media (max-width: 670px) {
  .form {
    flex-direction: column;
  }
}

@media (max-width: 481px) {
  .content {
    &__title {
      font-size: 30px;
    }
  }
}

@media (max-width: 391px) {
  .contact__container {
    padding: 40px 15px !important;
  }
  .content {
    row-gap: 20px;
    &__title {
      font-size: 24px;
    }
    &__description {
      font-size: 16px;
    }
  }
}