*, *::before, *::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  font-family: Museo Sans Cyrl;
  outline: none;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

html, body {
 min-width: 320px;
 scroll-behavior: smooth;
}

body {
  line-height: 1;
  position: relative;
  &.--show {
    overflow: hidden;
  }
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-track {
    background: linear-gradient(167.45deg, rgba(78, 75, 69, 0.8) 88.99%, rgba(78, 75, 69, 0) 154.01%);
    border-radius: 8px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: var(--dark-yellow);
    border-radius: 8px;
  }
}

.App {
  overflow: hidden;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

[class*="__container"] {
  max-width: 1350px;
  margin: 0 auto;
  padding: 0 15px;
}

.scroll-fix {
  position: absolute;
  width: calc((100vw - 100%));
}

.header.--show {
  overflow: hidden;
}