.catalog {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    background-image: url("../../../public/img/catalog/background.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    max-width: 1224px;
    max-height: 1224px;
    opacity: 0.6;
    z-index: -1;
    filter: drop-shadow(0px 0px 10px rgba(255, 194, 86, 0.05));
  }
  &__container {
    padding-top: 85px !important;
    padding-bottom: 95px !important;
  }
  &__content {
    display: flex;
    flex-direction: column;
  }
}

.content_catalog {
  &__title {
    font-weight: 900;
    font-size: 36px;
    color: var(--dark-gray);
    line-height: 42.19px;
    margin-bottom: 57px;
    text-align: center;
    & span {
      color: var(--dark-yellow);
      line-height: 42.19px;
    }
  }
  &__buttons {
    display: grid;
    grid-template-columns: repeat(4, auto);
    margin: 0 auto;
    width: 100%;
    max-width: 1145px;
    gap: 38px;
  }
  &__swiper {
    position: relative;
    margin-top: 61px;
    margin-bottom: 32px;
    padding: 0 110px;
  }
  &__description {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 47px;
  }
}

.buttons_catalog {
  &__button {
    display: block;
    padding: 22px 35px;
    border-radius: 5px;
    border: 2px solid var(--dark-yellow);
    color: var(--dark-yellow);
    font-size: 16px;
    line-height: 19.2px;
    background: none;
    font-weight: 900;
    transition: box-shadow var(--speed_04), background-color var(--speed_04), color var(--speed_04);
    cursor: pointer;
    &:hover {
      color: var(--white);
      background-color: var(--dark-yellow);
      box-shadow: 0px 4px 20px rgba(207, 165, 91, 0.5);
    }
  }
}

.swiper {
  &__buttonPrev,
  &__buttonNext {
    background: none;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
    & svg {
      width: 68px;
      height: 68px;
      fill: var(--dark-yellow);
      opacity: 0.3;
      cursor: pointer;
    }
  }
  &__buttonPrev {
    left: 0;
  }
  &__buttonNext {
    right: 0;
  }
}

.description_catalog {
  &__wrapper {
    display: flex;
    width: 100%;
    max-width: 1031px;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 15px;
  }
}

.title_description {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 2px;
  &__title:first-child {
    color: var(--dark-yellow);
    font-size: 30px;
    font-weight: 900;
    line-height: 36px;
  }
  &__title:last-child {
    color: var(--dark-gray);
    font-size: 18px;
    font-weight: 700;
    line-height: 21.6px;
  }
}

.description_description {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title:first-child {
    color: var(--dark-yellow);
    font-size: 30px;
    line-height: 36px;
    padding-bottom: 1px;
  }
  &__title:last-child {
    color: var(--dark-gray);
    font-size: 24px;
    line-height: 28.8px;
  }
}

@media (max-width: 942px) {
  .buttons_catalog {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }
}

@media (max-width: 731px) {
  .description_description {
    &__title:first-child {
      font-size: 22px;
    }
    &__title:last-child {
      font-size: 18px;
    }
  }
  .content_catalog__swiper {
    padding: 0 60px;
  }
  .swiper {
    &__buttonPrev,
    &__buttonNext {
      & svg {
        width: 40px;
        height: 40px;
      }
    }
  }
}

@media (max-width: 607px) {
  .buttons_catalog__button {
    padding: 15px 20px;
  }
  .content_catalog__swiper {
    padding: 0;
  }
  .swiper {
    &__buttonPrev,
    &__buttonNext {
      & svg {
        width: 40px;
        height: 40px;
        opacity: 1;
      }
    }
  }
}

@media (max-width:481px) {
  .content_catalog {
    &__title {
      font-size: 30px;
      margin-bottom: 30px;
    }
    &__swiper {
      margin-top: 30px;
    }
    &__description {
      row-gap: 30px;
    }
  }
  .buttons_catalog {
    grid-template-columns: repeat(1, 1fr);
  }
  .title_description {
    text-align: center;
    &__title:first-child {
      font-size: 24px;
    }
    &__title:last-child {
      font-size: 15px;
    }
  }
  .description_catalog__wrapper {
    justify-content: space-around;
  }
  .swiper {
    &__buttonPrev,
    &__buttonNext {
      & svg {
        width: 30px;
        height: 30px;
      }
    }
  }
}

@media (max-width: 391px) {
  .catalog {
    &__container {
      padding-top: 40px !important;
      padding-bottom: 40px !important;
    }
  }
  .content_catalog__title {
    font-size: 24px;
  }
  .title_description__title:first-child {
    font-size: 21px;
  }
  .description_description {
    &__title:first-child {
      font-size: 20px;
    }
    &__title:last-child {
      font-size: 16px;
    }
  }
  .swiper {
    &__buttonPrev,
    &__buttonNext {
      & svg {
        width: 20px;
        height: 20px;
      }
    }
  }
}