.footer {
  min-height: 152px;
  display: flex;
  align-items: center;
  &__container {
    width: 100%;
  }
  &__content {
    width: 100%;
    padding: 47px 0;
  }
}

.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  &__row {}
  &__title {
    font-size: 24px;
    color: var(--dark-gray);
    line-height: 28.8px;
  }
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 18px;
  &__icon {
    width: 39px;
    height: 39px;
    & svg {
      stroke: var(--dark-yellow);
    }
  }
  &__description {
    max-width: 203px;
    color: var(--dark-gray);
    line-height: 16.8px;
    font-size: 14px;
  }
}

@media (max-width: 549px) {
  .content {
    padding: 20px 0;
    flex-direction: column;
  }
}