.home {
  padding-top: 126px;
  min-height: 100vh;
  background:
    linear-gradient(167.45deg, rgba(78, 75, 69, 0.7) 88.99%, rgba(78, 75, 69, 0) 154.01%), 
    url("../../../public/img/home/background.png");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size:cover;
  &__container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    padding-bottom: 140px !important;
    padding-top: 186px !important;
  }  
  &__button {
    display: block;
    padding: 30px 45px;
    font-size: 16px;
    color: var(--white);
    line-height: 19.2px;
    background: none;
    border: 2px solid var(--dark-yellow);
    transition: background-color var(--speed_04), box-shadow var(--speed_04);
    border-radius: 5px;
    max-width: fit-content;
    position: relative;
    cursor: pointer;
    &::before {
      content: "";
      outline: 1px solid var(--dark-yellow);
      border-radius: 5px;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      transition: top var(--speed_04), left var(--speed_04), width var(--speed_04), height var(--speed_04);
    }
    &:hover {
      background-color: var(--dark-yellow);
      box-shadow: 0px 0px 60px rgba(255, 194, 86, 0.5);
      &::before {
        width: calc(100% + 14px);
        height: calc(100% + 14px);
        top: -7px;
        left: -7px;
      }
    }
  }
}

.content {
  display: flex;
  align-items: flex-end;
  column-gap: 429px;
  padding-bottom: 83px;
  &__title {
    max-width: 632px;
    font-size: 36px;
    color: var(--white);
    line-height: 43.2px;
    span {
      font-size: 40px;
      color: var(--dark-yellow);
      line-height: 48px;
      margin-left: 5px;
      margin-right: 5px;
    }
  }
  &__video {
    min-width: 63px;
    min-height: 63px;
    border-radius: 50%;
    position: relative;
    background: rgba(255, 190, 75, 0.3);
    animation: shadow 2.5s linear infinite;
    cursor: pointer;
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 3px solid var(--dark-yellow);
      animation: pulse-circle 2.5s linear infinite;
    }
    @keyframes shadow {
      0% {
        box-shadow: 0px 0px 0px var(--dark-yellow);
        filter: drop-shadow(0px 0px 0px rgba(0, 0, 0, 0.25));
      }
      50% {
        box-shadow: 0px 0px 50px var(--dark-yellow);
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
      }
      100% {
        box-shadow: 0px 0px 0px var(--dark-yellow);
        filter: drop-shadow(0px 0px 0px rgba(0, 0, 0, 0.25));
      }
    }
    @keyframes pulse-circle {
      0% {
        opacity: 0;
        transform: scale(1);
      }
      50% {
        opacity: 1;
      }
      100% {
        opacity: 0;
        transform: scale(1.5);
      }
    }
    svg {
      color: var(--white);
      width: 23px;
      height: 23px;
    }
  }
}

.home__info {
  display: grid;
  grid-template-columns: repeat(3, auto);
  padding-top: 156px;
}

.info {
  &__item {
    font-family: Roboto;
    position: relative;
    padding: 15px;
    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
    }
  }
}

.item {
  display: flex;
  align-items: center;
  &__circle {
    min-width: 14px;
    min-height: 14px;
    background-color: var(--dark-yellow);
    border-radius: 50%;
    margin-right: 33px;
    position: relative;
    &::before,
    &::after {
      content: "";
      position: absolute;
      background: var(--dark-yellow);
      width: 14px;
      height: 14px;
      border-radius: 50%;
      opacity: 0;
    }
    &::before {
      animation: pulse 2.5s linear infinite;
    }
    &::after {
      animation: pulse 2.5s linear infinite;
      animation-delay: 1s;
    }
    @keyframes pulse {
      0% {
        transform: scale(1);
        opacity: 0;
      }
      50% {
        opacity: 0.15;
      }
      100% {
        transform: scale(2.5);
        opacity: 0;
      }
    }
  }
  &__text {
    font-family: Roboto;
    font-weight: 400;
    font-size: 16px;
    line-height: 18.75px;
    max-width: 266px;
    color: var(--white);
  }
}

@media (max-width: 1091px) {
  .home__container {
    padding-top: 120px !important;
  }
  .content {
    column-gap: 200px;
  }
}

@media (max-width: 769px) {
  .home {
    padding-top: 90px;
    &__content {
      padding-bottom: 40px;
    }
    &__info {
      padding-top: 80px;
    }
    &__container {
      padding-top: 60px !important;
    }
  }
  .content {
    column-gap: 100px;
    flex-direction: column;
    align-items: flex-start;
    &__title {
      font-size: 25px;
      padding-bottom: 40px;
      max-width: none;
      span {
        font-size: 28px;
      }
    }
  }
}

@media (max-width: 726px) {
  .home {
    &__info {
      grid-template-columns: repeat(2, auto);
    }
    &__container {
      padding-bottom: 40px !important;
    }
  }
  .info {
    &__item {
      padding: 15px !important;
    }
  }
}

@media (max-width: 426px) {
  .home {
    &__info {
      grid-template-columns: repeat(1, auto);
    }
  }
}

@media (max-width: 341px) {
  .home {
    &__button {
      padding: 20px 25px;
    }
  }
  .content {
    &__title {
      font-size: 20px;
      span {
        font-size: 24px;
      }
    }
  }
}