.benefits {
  background-image: linear-gradient(179.9deg, rgba(49, 32, 2, 0) 0.09%, rgba(49, 32, 2, 0.6) 0.09%),
    url("../../../public/img/benefits/background.png");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  z-index: 1;
  &__container {
    padding-top: 80px !important;
    padding-bottom: 80px !important;
    min-height: 100vh;
    display: flex;
    flex: 0 1 50%;
    align-items: center;
    justify-content: space-between;
    column-gap: 60px;
  }
  &__card {
    position: relative;
    left: -20px;
    &::before {
      content: "";
      width: 100%;
      height: 100%;
      border-radius: 10px;
      border: 3px solid var(--dark-yellow);
      position: absolute;
      left: 25px;
      top: 25px;
      z-index: -1;
      filter: drop-shadow(0px 4px 30px rgba(207, 165, 91, 0.5));
    }
  }
}

.contentBenefits {
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  &__title {
    max-width: 477px;
    font-size: 36px;
    color: var(--white);
    line-height: 43.2px;
    margin-bottom: 23px;

    & span {
      color: var(--dark-yellow);
      line-height: 43.2px;
    }
  }
  &__description {
    font-size: 18px;
    color: var(--white);
    line-height: 21.6px;
    max-width: 638px;
  }
}

.cardBenefits {
  max-width: 447px;
  background-color: var(--white);
  padding: 36px 35px 43px 35px;
  border-radius: 10px;
  box-shadow: 0px 4px 50px rgba(207, 165, 91, 0.5);
  &__title {
    color: var(--dark-gray);
    margin-bottom: 23px;
    font-size: 24px;
    line-height: 28.8px;
    font-weight: 900;
  }
  &__description {
    font-size: 14px;
    line-height: 20px;
    color: var(--dark-gray);
  }
  &__points {
    display: flex;
    flex-direction: column;
    margin-top: 37px;
    margin-bottom: 54px;
    row-gap: 25px;
  }
  &__button {
    display: block;
    background-color: var(--dark-yellow);
    padding: 25px 90px;
    font-size: 18px;
    line-height: 21.6px;
    color: var(--white);
    font-weight: 900;
    border-radius: 5px;
    margin: 0 auto;
    transition: transform var(--speed_04);
    &:hover {
      transform: scale(1.025);
    }
  }
}

.itemPoints {
  display: flex;
  align-items: center;
  column-gap: 14px;
  row-gap: 25px;
  &__icon {
    & svg {
      stroke-width: 2;
      width: 16px;
      height: 16px;
      filter: drop-shadow(0px 4px 4px rgba(207, 165, 91, 0.5));
    }
  }
  &__description {
    font-size: 14px;
    color: var(--dark-gray);
    line-height: 16.8px;
    & span {
      font-family: Roboto;
      font-weight: 900;
      color: var(--dark-yellow);
      line-height: 16.41px;
    }
  }
}

@media (max-width: 955px) {
  .benefits__container {
    column-gap: 0;
    row-gap: 40px;
    flex-direction: column;
  }
}

@media (max-width: 521px) {
  .benefits__card {
    left: 0;
    &::before {
      display: none;
    }
  }
}

@media (max-width: 461px) {
  .contentBenefits {
    &__title {
      font-size: 27px;
      margin: 0;
    }
    row-gap: 20px;
  }
  .cardBenefits {
    &__button {
      padding: 25px 40px;
    }
  }
}

@media (max-width: 391px) {

  .benefits {
    &__container {
      padding-top: 40px !important;
      padding-bottom: 40px !important;
    }
    &__card {
      padding: 20px 20px 30px 20px;
    }
  }
  .contentBenefits__title {
    font-size: 25px;
  }
  .cardBenefits {
    &__title {
      font-size: 20px;
    }
    &__button {
      font-size: 16px;
    }
  }
}