.header {
  display: flex;
  align-items: flex-end;
  width: 100%;
  min-width: 320px;
  min-height: 126px;
  position: absolute;
  z-index: 100;
  transition: align-items var(--speed_04);
  &.fixed {
    position: fixed;
    background-color: var(--light-dark);
    align-items: center;
    backdrop-filter: blur(5px);
    box-shadow: 0 0 8px var(--color-9);
    animation: fixed 300ms;
    @keyframes fixed {
      0% {
        opacity: 0;
        top: -100px;
      }
      100% {
        opacity: 1;
        top: 0;
      }
    }
  }
  &__container {
    display: flex;
    align-items: center;
    gap: 77px;
    position: relative;
    width: 100%;
  }

  &__wrapper {
    gap: 115px;
    display: flex;
    align-items: center;
  }

  &__logo {
    font-size: 51px;
    font-weight: 900;
    color: var(--white);
    line-height: 61px;
    display: flex;
    flex-direction: column;

    & span {
      font-size: 27px;
      color: var(--dark-yellow);
      font-weight: 700;
      line-height: 32px;
    }
  }

  &__text {
    font-size: 18px;
    font-weight: 500;
    color: var(--white);
    max-width: 286px;
    width: 286px;
    height: 44px;
    line-height: 22px;
  }

  &__connection {
    display: flex;
    gap: 30px;
  }

  &__button_menu {
    width: 38px;
    height: 38px;
    position: relative;
    background: none;
    padding: 8px 0;
    z-index: 100;
    cursor: pointer;
    &.active {
      & span {
        background: none;
        &::before {
          transform: translate(0, -50%) rotate(45deg);
        }
        &::after {
          transform: translate(0, -50%) rotate(-45deg);
          max-width: none;
        }
      }
    }
    & span {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      height: 2px;
      background-color: var(--white);
      border-radius: 5px;
      margin-top: -1px;
      margin-left: -19px;
      transform: translate(0, -50%);
      transition: background-color var(--speed_04);
      &::before,
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 2px;
        background-color: var(--white);
        border-radius: 5px;
        margin-top: -1px;
        margin-left: -19px;
        transition: max-width var(--speed_04), transform var(--speed_04);
      }

      &::before {
        transform: translateY(-7px);
      }

      &::after {
        max-width: 25px;
        transform: translateY(7px);
      }
    }
  }
}

.connection {
  &__telephone {
    font-size: 24px;
    font-weight: 700;
    color: var(--white);
    align-self: center;
  }

  &__button {
    display: block;
    border-radius: 5px;
    border: 2px solid var(--yellow);
    color: var(--yellow);
    padding: 15px 25px;
    font-size: 14px;
    font-weight: 700;
    background: none;
    transition: background-color var(--speed_04), color var(--speed_04);
    filter: drop-shadow(0px 0px 30px var(--yellow));
    cursor: pointer;
    &::before {
      content: "";
      outline: 1px solid var(--dark-yellow);
      border-radius: 5px;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      transition: top var(--speed_04), left var(--speed_04), width var(--speed_04), height var(--speed_04);
    }
    &:hover {
      background-color: var(--dark-yellow);
      color: var(--white);
      &::before {
        width: calc(100% + 14px);
        height: calc(100% + 14px);
        top: -7px;
        left: -7px;
      }
    }
  }
}

.menu {
  position: fixed;
  top: 0;
  right: 0;
  min-width: 320px;
  height: 100vh;
  background: linear-gradient(167.45deg, rgba(78, 75, 69, 0.8) 88.99%, rgba(78, 75, 69, 0) 154.01%);
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(100%);
  transition: transform var(--speed_04);
  z-index: 99;
  &.open {
    transform: translateX(0);
  }
  &__item {
    margin: 30px 0;
    padding: 15px 40px;
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    & a {
      display: block;
      color: var(--white);
      font-size: 24px;
      font-weight: 900;
      letter-spacing: 2px;
      transition: color var(--speed_04);
      text-align: center;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 100%;
      transition: width var(--speed_04);
      background-color: var(--white);
      z-index: -1;
    }
    &:hover {
      &::before {
        width: 100%;
      }
      & a {
        color: var(--dark-gray);
      }
    }
  }
}

@media (max-width: 1091px) {
  .header {
    &__container {
      gap: 40px;
    }

    &__wrapper {
      gap: 80px;
    }

    &__logo {
      font-size: 40px;

      span {
        font-size: 20px;
      }
    }
  }

  .connection {
    &__telephone {
      font-size: 18px;
    }
  }
}

@media (max-width: 921px) {
  .connection {
    &__button {
      padding: 10px 15px;
    }
  }
  .header {
    &__text {
      display: none;
    }
    &__container {
      justify-content: space-between;
    }
  }
}

@media (max-width: 769px) {
  .header {
    display: flex;
    align-items: center;
    min-height: 90px;
  }
  .header {
    &__container {
      width: 100%;
      padding: 10px 15px !important;
    }
    &__wrapper {
      gap: 20px;
    }
    &__logo {
      font-size: 30px;
      line-height: 41px;
      span {
        font-size: 15px;
        line-height: 20px;
      }
    }
  }
}

@media (max-width: 541px) {
  .header {
    &__wrapper {
      gap: 40px;
    }
    &__container {
      gap: 10px;
    }
    &__connection {
      flex-direction: column;
      gap: 10px;
    }
  }
}

@media (max-width: 426px) {
  .header {
    &__connection  {
      display: none;
    }
  }
  .menu__item {
    font-size: 20px;
  }
}