.about {
  position: relative;
  overflow: hidden;
  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    max-width: 1224px;
    max-height: 1224px;
    z-index: -1;
  }
  &::before {
    background-image: url("../../../public/img/about/background.png");
    background-repeat: no-repeat;
    background-size: contain;
    top: -9%;
    right: -33%;
  }
  &::after {
    background-image: url("../../../public/img/about/background_2.png");
    background-repeat: no-repeat;
    background-size: contain;
    bottom: -20%;
    left: -8%;
    transform: rotate(-15deg);
  }
  &__container {
    padding-top: 78px !important;
    padding-bottom: 118px !important;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.content {
  display: flex;
  flex-direction: column;
  row-gap: 188px;
  &__title {
    text-align: center;
    max-width: 694px;
    font-size: 36px;
    line-height: 43.2px;
    color: var(--dark-gray);
    font-weight: 900;
    & span {
      font-family: Roboto;
      color: var(--dark-yellow);
    }
  }
  &__items {
    width: 100%;
    flex: 0 1 50%;
    display: flex;
    flex-direction: column;
    row-gap: 215px;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    column-gap: 85px;
  }
}

.item {
  &__content {
    display: flex;
    flex-direction: column;
    row-gap: 45px;
    position: relative;
    &::before {
      content: "01";
      position: absolute;
      top: -137px;
      left: 0;
      font-family: Roboto;
      font-size: 96px;
      line-height: 112px;
      text-transform: uppercase;
      opacity: 0.3;
      color: var(--dark-yellow);
    }
  }
  &__title {
    font-size: 30px;
    line-height: 36px;
    text-transform: uppercase;
    color: var(--dark-gray);
  }
  &__button {
    display: block;
    max-width: 420px;
    padding: 31px 66px;
    border-radius: 5px;
    box-shadow: 0px 20px 40px rgba(207, 165, 91, 0.4);
    background-color: var(--dark-yellow);
    color: var(--white);
    line-height: 21px;
    font-size: 18px;
    transition: transform var(--speed_04);
    cursor: pointer;
    &:hover {
      transform: translateY(-8px);
    }
  }
  &__image {
    width: fit-content;
    box-shadow: 0px 5px 50px rgba(60, 60, 60, 0.4);
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(207, 165, 91, 0.2), rgba(207, 165, 91, 0.2));
    }
    & img {
      display: block;
      max-width: 649px;
      height: 100%;
      object-fit: cover;
    }
  }
}

.row {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  &__wrapper {
    display: flex;
    align-items: center;
    column-gap: 27px;
  }
  &__icon {
    & svg {
      font-size: 25px;
      color: var(--dark-yellow);
      filter: drop-shadow(0px 4px 1px rgba(207, 165, 91, 0.5));
    }
  }
  &__text {
    font-size: 18px;
    line-height: 22px;
    color: var(--dark-gray);
    max-width: 408px;
    & span {
      color: var(--dark-yellow);
      font-family: Roboto;
      line-height: 22px;
    }
  }
}

@media (max-width: 1010px) {
  .content {
    &__item {
      column-gap: 40px;
    }
  }
}

@media (max-width: 966px) {
  .content {
    &__items {
      margin-bottom: 50px;
    }
    &__item {
      flex-direction: column;
      margin: 0 auto;
      row-gap: 50px;
      &:nth-child(2) {
        .item__image {
          order: 2;
        }
      }
    }
  }
  .row {
    &__text {
      max-width: none;
    }
  }
  .item__image {
    & img {
      max-width: 100%;
    }
  }
}

@media (max-width: 546px) {
  .content {
    &__items {
      row-gap: 100px;
    }
    &__title {
      font-size: 30px;
    }
  }
  .item {
    &__content {
      &::before {
        top: -20px;
        left: 100%;
        transform: translateX(-100%);
      }
    }
  }
  .about {
    &__content {
      row-gap: 60px;
    }
  }
}

@media (max-width: 477px) {
  .item {
    &__content::before {
      display: none;
    }
    &__title {
      text-align: center;
      font-size: 22px;
    }
    &__button {
      margin: 0 auto;
    }
  }
  .content {
    &__title {
      font-size: 28px;
    }
  }
}

@media (max-width: 440px) {
  .item__button {
    padding: 20px 40px;
  }
}

@media (max-width: 391px) {
  .about__container {
    padding-top: 40px !important;
    padding-bottom: 40px !important;
  }
}